.reserve{
  padding: 36px 0 138px;
  &__wrap{
    padding: 0 32px 58px;
    background: #FFFFFF;
    box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    margin-top: 23px;
    min-height: 504px;
    display: flex;
    flex-direction: column;
  }
  &__tabs{
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-shrink: 0;
  }
  &__tab{
    border-radius: 0px 0px 20px 20px;
    font-size: 24px;
    color: #d4d4d4;
    padding: 15px 5px 26px;
    flex-grow: 1;
    max-width: 240px;
    font-family: $font2;
    text-align: center;
    cursor: pointer;
    &._active{
      font-weight: 700;
      color: #fff;
      background-color: $primary;
      pointer-events: none;
    }
    &._disabled{
      pointer-events: none;
    }
  }
  &__steps{
    margin-top: 39px;
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
}

.step{
  flex-grow: 1;
  display: none;
  flex-direction: column;
  &._active{
    display: flex;
  }
  &__form-grid{
    display: grid;
    grid-template-columns: 24% 24% 38%;
    gap: 45px 7%;
  }
  &__btn{
    width: 278px;
    height: 42px;
    margin-top: 51px;
    font-size: 16px;
  }
  &__aparts{
    display: grid;
    grid-template-columns: repeat(5, calc(20% - (80px / 5)));
    gap: 20px;
  }
  &__nav{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .fg{
    max-width: 265.44px;
    &--text{
      max-width: 100%;
    }
  }
  @media screen and (min-width: 767px){
    .apart-card{
      border: 1px solid #DCDCDC;
      border-radius: 10px;
      overflow: hidden;
      justify-content: unset;
      padding: 0;
      color: $black;
      min-height: unset;
      &._active{
        border-color: $primary;
      }
      &:before{
        display: none;
      }
      &__slider{
        position: relative;
        height: unset;
      }
      .swiper{
        &-button{
          &-prev,
          &-next{
            width: 26px;
            height: 26px;
            margin-top: 0;
            transform: translateY(-50%);
            svg{
              width: 6px;
              height: auto;
            }
          }
          &-prev{
            left: 6px;
          }
          &-next{
            right: 6px;
          }
        }
      }
      &__rating{
        display: none;
      }
      &__img{
        padding-bottom: 68.25%;
        position: relative;
        img{
          position: absolute;
          width: 100%;
          height: 100%;
          object-fit: cover;
        }
      }
      &__people{
        padding: 0 7px 0 0;
        height: unset;
        background-color: transparent;
        margin: 12px 0 0 auto;
        font-size: 14px;
        line-height: 18px;
        display: inline-flex;
        justify-content: flex-end;
        svg{
          width: 22px;
          height: auto;
          margin-right: 7px;
        }
      }
      &__content{
        padding: 0;
        margin: -27px 0 0 0;
      }
      &__title{
        font-size: 20px;
        line-height: 100%;
        text-align: left;
        padding: 7px 95px 0 9px;
      }
      &__desc{
        display: none;
        &-full{
          display: none;
        }
      }
      &__bottom{
        display: block;
        padding: 4px 18px 12px;
        margin-top: auto;
      }
      &__price{
        font-size: 14px;
        line-height: 26px;
        text-align: center;
        b{
          font-size: 20px;
        }
      }
      &__link{
        width: 100%;
        margin: 5px 0 0;
        border-radius: 90px;
        font-weight: 700;
        font-size: 16px;
      }
    }
  }
  .apart-card{
    &._active{
      .apart-card__link{
        background-color: $primary;
        color: #fff;
      }
    }
    &__link{
      box-shadow: none;
      background-color: transparent;
      border: 1px solid $primary;
      color: $primary;
      &:hover{
        color: #fff;
        background-color: $primary;
      }
    }
  }
}

.step-info{
  &__grid{
    display: grid;
    grid-template-columns: 270px 360px;
    gap: 80px;
  }
  &__item{
    display: grid;
    grid-template-columns: 180px auto;
    gap: 5px;
    font-size: 18px;
    line-height: 120%;
    font-family: $font2;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
  &__title{
    color: #929292;
  }
  &__desc{
    font-weight: 700;
  }
  &__sum{
    display: flex;
    align-items: flex-end;
    font-family: $font2;
    font-weight: 700;
    margin-top: 30px;
    &-title{
      font-size: 18px;
    }
    &-price{
      margin-left: 35px;
      font-size: 26px;
      color: $red;
    }
  }
}

.step-thank{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  &__icon{
    display: block;
    margin: 0 0 29px;
  }
  &__title{
    text-align: center;
  }
  &__nav{
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &__btn{
    width: 278px;
    height: 42px;
    margin: 0 8px;
    &--light{
      background: #FFE600;
    }
  }
}


@media screen and (max-width: 1300px){
  .reserve{
    padding: 42px 0 70px;
    &__wrap{
      padding-left: 15px;
      padding-right: 15px;
    }
  }

  .step{
    &__form{
      &-grid{
        grid-template-columns: 27% 27% 27%;
        gap: 45px calc(19% / 2);
      }
    }
    &__aparts{
      grid-template-columns: repeat(5, calc(20% - (24px / 5)));
      gap: 6px;
    }
  }
}


@media screen and (max-width: 1024px){
  .reserve{
    padding: 35px 0 68px;
    &__title{
      align-items: unset;
      &:before,
      &:after{
        height: 50px;
      }
    }
    &__wrap{
      min-height: 395px;
    }
    &__tab{
      font-size: 18px;
      line-height: 144%;
    }
  }

  .step{
    &__form{
      &-grid{
        grid-template-columns: 28.25% 28.25% 28.25%;
        gap: 45px calc(15.25% / 2);
      }
    }
    .fg{
      label{
        white-space: nowrap;
      }
    }
    @media screen and (min-width: 767px){
      &__aparts{
        display: flex;
        justify-content: center;
        gap: unset;
        flex-wrap: wrap;
        width: calc(100% + 25px);
        margin-top: -21px;
        .apart-card{
          margin: 21px 25px 0 0;
          width: calc(100% / 3 - 25px);
        }
      }
    }
  }

  .step-info{
    &__grid{
      grid-template-columns: 1fr 1fr;
      gap: 20px;
    }
  }

  .step-thank{
    &__nav{
      margin-top: 28px;
    }
  }
}


@media screen and (max-width: 767px){
  .reserve{
    padding-bottom: 0;
    &__wrap{
      box-shadow: none;
    }
  }

  .step{
    &__form{
      &-grid{
        grid-template-columns: 100%;
        gap: 14px;
      }
    }
    .fg{
      max-width: 100%;
    }
    &__btn{
      margin-top: 22px;
      width: 100%;
    }
    &__aparts{
      grid-template-columns: 100%;
      gap: 24px;
    }
    &__nav{
      flex-direction: column;
      margin-top: 24px;
    }
  }

  .step-info{
    &__grid{
      grid-template-columns: 100%;
    }
    &__item{
      display: block;
    }
    &__title{
      display: inline-block;
      padding-right: 8px;
    }
  }

  .step-thank{
    &__nav{
      margin-top: 25px;
      display: block;
      width: 100%;
      padding: 0 13px;
    }
    &__btn{
      width: 100%;
      margin: 25px 0 0;
    }
  }
}


@media screen and (max-width: 600px){
  .reserve{
    padding-bottom: 55px;
    &__wrap{
      margin-top: 20px;
      padding: 0 19px 55px;
      width: calc(100% + 30px);
      margin-left: -15px;
      min-height: 376px;
    }
    &__tabs{
      width: calc(100% + 38px);
      margin-left: -19px;
    }
    &__tab{
      display: none;
      max-width: 100%;
      width: 100%;
      padding: 15px 35px 26px 17px;
      background-image: url(../img/tab-arrow.svg);
      background-repeat: no-repeat;
      background-position: right 14px top 25px;
      &._active{
        display: flex;
      }
    }
  }
}
