.display2{
  font-weight: 700;
  font-size: 50px;
  line-height: 126%;
  font-family: $font2;
  text-transform: uppercase;
}

.decor-display{
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  &:before,
  &:after{
    content: '';
    width: 181px;
    height: 35.35px;
    flex-shrink: 0;
    background-size: 100% auto;
    background-position: center;
    background-repeat: no-repeat;
  }
  &:before{
    background-image: url(../img/decor.svg);
    margin-right: 42px;
  }
  &:after{
    background-image: url(../img/decor2.svg);
    margin-left: 42px;
  }
}

.body-text{
  font-weight: 500;
  font-size: 18px;
  line-height: 156%;
  p{
    line-height: inherit;
  }
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 1300px){
  .display2{
    font-size: 44px;
    line-height: 125%;
  }
}

@media screen and (max-width: 1199px){
  .display2{
    font-size: 40px;
  }

  .decor-display{
    &:before,
    &:after{
      width: 143px;
      height: 27.9px;
    }
    &:before{
      margin-right: 19px;
    }
    &:after{
      margin-left: 19px;
    }
  }
}

@media screen and (max-width: 767px){
  .display2{
    font-size: 36px;
  }

  .decor-display{
    &:before,
    &:after{
      display: none;
    }
  }
}
