@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';
@import 'components/_input.scss';


.rating {
    display: flex;
    align-items: center;
    flex-direction: row-reverse;
    &--read-only{
        pointer-events: none;
    }
    &__item {
        transition: all .3s;
        font-size: 20px;
        color: #E1E1E1;
        &:not(:first-child) {
            margin-right: 6px;
        }
        &:hover {
            cursor: pointer;
            color: #FFE600;
            &~.rating__item {
                color: #FFE600;
            }
        }
    }
}

.rating[data-total-rating="1"] .rating__item:nth-child(n+5),
.rating[data-total-rating="2"] .rating__item:nth-child(n+4),
.rating[data-total-rating="3"] .rating__item:nth-child(n+3),
.rating[data-total-rating="4"] .rating__item:nth-child(n+2),
.rating[data-total-rating="5"] .rating__item:nth-child(n+1) {
    color: #FFE600;
}

.video{
    position: relative;
    padding-bottom: 64.9%;
    video{
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.swiper-pagination{
    &-bullet{
        width: 16px;
        height: 16px;
        margin: 0 4.5px !important;
        opacity: 1;
        background-color: transparent;
        border: 1px solid $primary;
        border-radius: 50%;
        &-active{
            background-color: $primary;
        }
    }
}


@media screen and (max-width: 1199px){
    .video{
        padding-bottom: 57.144%;
    }
}


@media screen and (max-width: 576px){
    .video{
        width: calc(100% + 30px);
        margin-left: -15px;
        padding-bottom: 148.28%;
    }

    .swiper-pagination{
        &-bullet{
            width: 7.4px;
            height: 7.4px;
            margin: 0 2.08px !important;
        }
    }
}
