.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  padding: 20px 0;
  &__inner{
    display: flex;
  }
  &__logo{
    flex-shrink: 0;
    display: block;
    width: 105px;
    margin-right: 88px;
    img{
      filter: drop-shadow(0px 0px 10px rgba(0, 0, 0, 0.25));
      width: 100%;
      display: block;
    }
  }
  &__mob{
    padding: 36px 0;
    flex-grow: 1;
  }
  &__wrap{
    display: flex;
    align-items: center;
    width: 100%;
  }
  &__buttons{
    display: flex;
    align-items: center;
    margin-left: auto;
  }
  &__btn{
    margin-left: 29px;
    &:hover{
      img{
        transform: translateY(-10px);
      }
    }
    &--yellow{
      img{
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
      }
    }
    img{
      transition: all .3s;
    }
  }
  &__contacts{
    display: flex;
    align-items: center;
    margin: 0 3px 0 62px;
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    color: #FFFFFF;
    font-family: $font2;
    &-item{
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 38px;
      }
    }
    &-icon{
      flex-shrink: 0;
      margin-right: 6px;
    }
  }
}

.nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 700;
    font-size: 20px;
    line-height: 25px;
    color: #FFFFFF;
    &:not(:last-child){
      margin-right: 85px;
    }
    &:hover{
      text-decoration-line: underline;
      color: $primary;
    }
  }
}


@media screen and (max-width: 1300px){
  .header{
    &__contacts{
      &-item{
        &:not(:last-child){
          margin-right: 24px;
        }
      }
    }
  }

  .nav{
    &__link{
      &:not(:last-child){
        margin-right: 31px;
      }
    }
  }
}


@media screen and (max-width: 1199px){
  .header{
    padding: 18px 0;
    &__logo{
      width: 91px;
      margin-right: 15px;
    }
    &__wrap{
      flex-wrap: wrap;
      justify-content: flex-end;
    }
    &__mob{
      padding: 15px 0;
    }
    &__contacts{
      margin: 0;
      &-item{
        &:not(:last-child){
          margin-right: 21px;
        }
      }
    }
    &__buttons{
      margin: 0;
    }
    &__btn{
      margin-left: 16px;
    }
  }

  .nav{
    width: 100%;
    justify-content: flex-end;
    margin-bottom: 23px;
    &__link{
      &:not(:last-child){
        margin-right: 56px;
      }
    }
  }
}


@media screen and (max-width: 767px){
  .header{
    padding: 20px 0;
    &__inner{
      position: relative;
    }
    &__logo{
      width: 52px;
      margin-right: 21px;
    }
    &__mob{
      padding: 0;
    }
    &__contacts{
      display: block;
      &-item{
        font-size: 16px;
        line-height: 125%;
        align-items: unset;
        &:not(:last-child){
          margin: 0 0 3px;
        }
        &:last-child{
          padding-right: 75px;
        }
      }
      &-icon{
        flex-shrink: 0;
        width: 15px;
        height: 17px;
        margin-right: 6px;
        transform: translateY(1.5px);
        img{
          max-width: 100%;
          max-height: 100%;
        }
      }
    }
    &__buttons{
      position: absolute;
      right: 0;
      bottom: 0;
    }
    &__btn{
      margin-left: 14px;
      img{
        width: 23.93px;
      }
    }
  }

  .nav{
    justify-content: space-between;
    margin-bottom: 8px;
    &__link{
      font-size: 16px;
      line-height: 125%;
      &:not(:last-child){
        margin-right: 10px;
      }
    }
  }
}
