.footer{
  background: #383838;
  position: relative;
  padding: 20px 0 31px;
  color: #FFFFFF;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url(../img/footer-bg.webp);
  }
  .container{
    position: relative;
    z-index: 2;
  }
  &__scroll-top{
    display: none;
  }
  &__inner{
    display: flex;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 88px;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__right{
    flex-grow: 1;
    max-width: 886px;
  }
  &__aparts{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__apart{
    font-weight: 600;
    font-size: 20px;
    line-height: 200%;
  }
  &__wrap{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    margin-top: 20px;
  }
  &__buttons{
    display: flex;
    align-items: center;
  }
  &__btn{
    margin-left: 29px;
    &:hover{
      img{
        transform: translateY(-10px);
      }
    }
    &--yellow{
      img{
        box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.15);
      }
    }
    img{
      transition: all .3s;
    }
  }
  &__contacts{
    &-item{
      font-weight: 700;
      font-family: $font2;
      font-size: 20px;
      line-height: 25px;
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 38px;
      }
    }
    &-icon{
      flex-shrink: 0;
      margin-right: 6px;
    }
  }
  &__bottom{
    display: flex;
    align-items: center;
    margin-top: 25px;
    justify-content: space-between;
  }
  &__link{
    color: #EDEDED;
    font-size: 16px;
    line-height: 212%;
  }
  &__co{
    font-weight: 500;
    font-size: 14px;
    line-height: 186%;
    color: $primary;
  }
}


@media screen and (max-width: 1100px){
  .footer{
    &__logo{
      width: 71px;
      margin-right: 22px;
    }
    &__bottom{
      margin-top: 40px;
    }
  }
}


@media screen and (max-width: 767px){
  .footer{
    padding: 22px 0 5px;
    &__inner{
      position: relative;
    }
    &__scroll-top{
      display: flex;
      position: absolute;
      right: 0;
      top: -28px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
    }
    &__logo{
      width: 77px;
      margin-right: 20px;
    }
    &__right{
      display: flex;
      flex-direction: column;
    }
    &__wrap{
      order: -1;
      min-height: 133px;
      display: block;
      margin: 0 0 13px;
    }
    &__contacts{
      &-item{
        font-size: 16px;
        line-height: 125%;
        align-items: unset;
        &:not(:last-child){
          margin: 0 0 24px;
        }
      }
      &-icon{
        flex-shrink: 0;
        width: 15px;
        height: 17px;
        margin-right: 6px;
        transform: translateY(1.5px);
        img{
          max-width: 100%;
          max-height: 100%;
        }
      }
    }
    &__buttons{
      margin-top: 26px;
    }
    &__btn{
      margin: 0 24px 0 0;
    }
    &__aparts{
      width: calc(100% + 97px);
      margin-left: -97px;
      display: grid;
      grid-template-columns: 1fr .4fr;
    }
    &__apart{
      font-weight: 400;
      line-height: 238%;
    }
    &__bottom{
      display: block;
      margin: 7px 0 0 -97px;
      width: calc(100% + 97px);
      text-align: center;
    }
    &__link{
      font-size: 14px;
      line-height: 171%;
    }
  }
}
