.main{
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  padding: 308px 0 108px;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    background: linear-gradient(180deg, #242424 0%, rgba(36, 36, 36, 0) 34.07%);
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    pointer-events: none;
  }
  &__inner{
    padding: 55px 55px 74px;
    background: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(4px);
    border-radius: 15px;
    width: 972px;
    max-width: 100%;
    margin: 0 auto;
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  &__title{
    font-weight: 700;
    font-size: 80px;
    line-height: 112%;
    font-family: $font2;
    text-transform: uppercase;
  }
  &__btn{
    max-width: 100%;
    width: 518px;
    margin-top: 39px;
    height: 93px;
    font-size: 28px;
  }
}

.apart{
  padding: 52px 0 133px;
  &__grid{
    margin-top: 44px;
    display: grid;
    grid-template-columns: repeat(3, calc(33.33% - (40px / 3)));
    gap: 20px;
  }
  @media screen and (min-width: 767px){
    .apart-card{
      &:nth-child(1){
        grid-column: span 2;
        .apart-card__price{
          font-size: 20px;
          b{
            font-size: 28px;
          }
        }
        .apart-card__desc{
          max-width: 345px;
        }
      }
    }
  }
}

.advantages{
  padding: 106px 0;
  background-image: url(../img/advantages-bg.webp);
  background-size: cover;
  background-position: center;
  position: relative;
  &:before{
    content: '';
    background: rgba(22, 22, 22, 0.7);
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  &__inner{
    padding: 70px 35px 70px;
    background: rgba(255, 255, 255, 0.7);
    backdrop-filter: blur(5px);
    border-radius: 20px;
    position: relative;
    z-index: 2;
  }
  &__title{
    font-size: 44px;
    line-height: 125%;
    &:before{
      margin-right: 33px;
    }
    &:after{
      margin-left: 33px;
    }
  }
  &__grid{
    margin-top: 62px;
    display: grid;
    grid-template-columns: repeat(4, calc(25% - (90px / 4)));
    gap: 30px;
  }
}

.info{
  padding: 63px 0 30px;
  &__inner{
    display: grid;
    grid-template-columns: auto 57.6%;
    gap: 24px;
  }
}

.boss{
  padding: 30px 0;
  &__inner{
    display: grid;
    grid-template-columns: auto 42.4%;
    background: #FFFFFF;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.15);
    border-radius: 20px;
    overflow: hidden;
  }
  &__content{
    padding: 26px 37px;
  }
  &__img{
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__title{
    font-weight: 700;
    font-size: 30px;
    line-height: 38px;
    text-transform: uppercase;
    font-family: $font2;
    display: flex;
    align-items: center;
    margin-bottom: 27px;
    &:after{
      content: '';
      width: 117px;
      height: 22.85px;
      flex-shrink: 0;
      background-size: 100% auto;
      background-position: center;
      background-image: url(../img/decor2.svg);
      margin-left: 22px;
    }
  }
}

.feedback{
  padding: 30px 0 20px;
  overflow: hidden;
  &__title{
    &:before{
      margin-right: 22px;
    }
    &:after{
      margin-left: 22px;
    }
  }
  &__slider{
    margin-top: 50px;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
      opacity: 0;
      pointer-events: none;
      &-active,
      &-next,
      &-next+.swiper-slide{
        opacity: 1;
        pointer-events: unset;
      }
    }
    &-pagination{
      position: static;
      margin-top: 43px;
    }
  }
}

.text-block{
  padding: 20px 0 55px;
}


@media screen and (max-width: 1300px){
  .main{
    padding: 190px 0 90px;
  }

  .apart{
    padding: 65px 0 123px;
  }

  .advantages{
    &__inner{
      padding: 80px 10px;
    }
    &__title{
      font-size: 40px;
      align-items: unset;
      &:before{
        margin-right: 20px;
        height: 50px;
      }
      &:after{
        margin-left: 20px;
        height: 50px;
      }
    }
    &__grid{
      grid-template-columns: repeat(4,calc(25% - 15px));
      gap: 20px;
    }
  }

  .info{
    padding: 50px 0 27px;
    &__inner{
      gap: 18px;
    }
    @media screen and (min-width: 1199px){
      &__text{
        font-size: 17px;
        line-height: 141%;
      }
    }
  }

  .boss{
    padding: 27px 0 15px;
  }

  .feedback{
    &__title{
      font-size: 40px;
      align-items: unset;
      &:before,
      &:after{
        height: 50px;
      }
    }
  }
}

@media screen and (max-width: 1199px){
  .main{
    padding: 154px 0 45px;
    &__title{
      font-size: 60px;
      line-height: 117%;
    }
    &__btn{
      margin-top: 79px;
    }
  }

  .apart{
    padding: 51px 0 86px;
  }

  .info{
    padding: 28px 0 14px;
    &__inner{
      grid-template-columns: 100%;
    }
  }

  .boss{
    padding: 14px 0;
    &__inner{
      grid-template-columns: 100%;
    }
    &__img{
      order: -1;
    }
    &__title{
      margin-bottom: 20px;
    }
  }

  .text-block{
    padding: 14px 0 50px;
    &__content{
      font-size: 16px;
      line-height: 125%;
    }
  }
}

@media screen and (max-width: 1100px){
  .apart{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 10px));
      gap: 30px 20px;
    }
  }

  .advantages{
    padding-bottom: 66px;
    &__inner{
      padding: 27px 20px 40px;
    }
    &__grid{
      margin: 27px auto 0;
      justify-content: space-between;
      grid-template-columns: 262px 262px;
      gap: 33px;
      max-width: 600px;
    }
  }

  .feedback{
    padding: 30px 0 15px;
    &__slider{
      margin-top: 33px;
    }
  }
}


@media screen and (max-width: 767px){
  .main{
    padding: 129px 0 26px;
    &__inner{
      padding: 30px 10px;
    }
    &__title{
      font-size: 36px;
      line-height: 128%;
    }
    &__btn{
      margin-top: 22px;
      height: 63px;
      font-size: 20px;
    }
  }

  .apart{
    padding: 6px 0 26px;
    &__grid{
      grid-template-columns: 100%;
      gap: 24px;
      margin-top: 13px;
    }
  }

  .advantages{
    padding: 41px 0 32px;
    &__inner{
      padding: 47px 10px 20px;
    }
    &__title{
      font-size: 30px;
      line-height: 120%;
    }
    &__grid{
      grid-template-columns: 1fr 1fr;
    }
  }

  .info{
    padding-top: 21px;
    &__inner{
      gap: 23px;
    }
    &__text{
      font-size: 16px;
      line-height: 138%;
    }
  }

  .boss{
    padding: 19px 0 18px;
    .container{
      padding: 0;
    }
    &__title{
      margin-bottom: 10px;
      &:after{
        display: none;
      }
    }
    &__content{
      padding: 13px 27px;
    }
    &__desc{
      font-size: 16px;
      line-height: 125%;
    }
  }

  .feedback{
    padding-top: 18px;
  }

  .text-block{
    padding-bottom: 40px;
    &__content{
      font-size: 14px;
      line-height: 129%;
    }
  }
}


@media screen and (max-width: 576px){
  .advantages{
    &__grid{
      grid-template-columns: 100%;
      gap: 20px;
    }
  }
}
