.catalog-item{
  padding: 36px 0 40px;
  &__inner{
    background: #FFFFFF;
    box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.1);
    border-radius: 20px;
    overflow: hidden;
    display: grid;
    grid-template-columns: 57.61% auto;
  }
  &__slider{
    overflow: hidden;
    position: relative;
    height: 100%;
  }
  &__img{
    position: relative;
    height: 100%;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: linear-gradient(180deg, rgba(25, 25, 25, 0) 0%, rgba(25, 25, 25, 0.63) 100%);
      z-index: 2;
      pointer-events: none;
    }
    img{
      height: 100%;
      width: 100%;
      object-fit: cover;
    }
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }

    &-pagination{
      bottom: 18px;
      &-bullet{
        border-color: #fff;
        &-active{
          background-color: #fff;
        }
      }
    }
  }
  &__content{
    padding: 28px 18px 35px;
  }
  &__title{
    font-weight: 700;
    font-size: 36px;
    line-height: 38px;
    font-family: $font2;
    display: flex;
    align-items: center;
    &:after{
      content: '';
      width: 91.4px;
      height: 17.85px;
      flex-shrink: 0;
      background-size: 100% auto;
      background-position: center;
      background-image: url(../img/decor2.svg);
      margin-left: 24px;
    }
  }
  &__rating{
    justify-content: flex-end;
    margin-top: 14px;
  }
  &__desc{
    font-weight: 300;
    font-size: 14px;
    line-height: 143%;
    margin-top: 18px;
  }
  &__people{
    display: flex;
    align-items: center;
    margin-top: 8px;
    color: $red;
    font-weight: 700;
    font-size: 17px;
    line-height: 18px;
    font-family: $font2;
    svg{
      margin-right: 10px;
    }
  }
  &__price{
    font-weight: 700;
    font-size: 20px;
    line-height: 26px;
    white-space: nowrap;
    margin-top: 34px;
    font-family: $font2;
    b{
      font-size: 34px;
      font-weight: inherit;
      color: $primary;
    }
  }
  &__btn{
    margin-top: 33px;
    width: 278px;
    height: 42px;
    font-size: 16px;
  }
}

.apart-collection{
  padding: 40px 0 102px;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - 15px));
    gap: 20px;
    margin-top: 45px;
  }
  @media screen and (min-width: 1024px){
    .apart-card{
      &__top,
      &__bottom{
        flex-direction: column;
      }
      &__rating{
        margin-top: 20px;
      }
      &__link{
        margin-top: 23px;
      }
    }
  }
}

.apart--page{
  padding: 36px 0 88px;
}


@media screen and (max-width: 1300px){
  .catalog-item{
    padding-bottom: 23px;
  }

  .apart-collection{
    padding: 23px 0 63px;
    @media screen and (min-width: 1024px){
      .swiper{
        &-button{
          &-prev,
          &-next{
            width: 25px;
            height: 25px;
            svg{
              width: 5px;
              height: auto;
            }
          }
          &-prev{
            left: 6px;
          }
          &-next{
            right: 6px;
          }
        }
      }
      .apart-card__desc{
        max-width: 157px;
        font-size: 16px;
        line-height: 125%;
      }
    }
  }
}


@media screen and (max-width: 1024px){
  .catalog-item{
    padding: 35px 0 14px;
    &__inner{
      grid-template-columns: 100%;
    }
    &__content{
      padding: 28px 21px 28px 31px;
    }
    &__desc{
      margin-top: 12px;
    }
    &__people{
      margin-top: 20px;
    }
    &__price{
      margin-top: 38px;
    }
  }

  .apart-collection{
    padding: 14px 0 37px;
    &__grid{
      margin-top: 24px;
      grid-template-columns: repeat(2,calc(50% - 10px));
    }
  }
}


@media screen and (max-width: 768px){
  .apart-collection{
    &__grid{
      grid-template-columns: 100%;
      gap: 24px;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    padding: 19px 0 15px;
    &__inner{
      width: calc(100% + 30px);
      margin-left: -15px;
      border-radius: 0;
    }
    &__content{
      padding: 20px 20px 30px;
    }
    &__title{
      &:after{
        display: none;
      }
    }
    &__price{
      margin-top: 20px;
    }
    .swiper-pagination{
      bottom: 9px;
    }
  }
}
